'use client';

import React from 'react';
import { useCGPA } from '@/components/shared/CGPAProvider';
import PredictiveCGPADashboard from '@/components/predictive-cgpa/PredictiveCGPADashboard';

export default function PredictPage() {
    const {
        domain,
        userGrades,
        overrides,
        updateOverride,
        clearOverrides,
        isInitialized
    } = useCGPA();

    if (!isInitialized) return null;

    return (
        <div className="space-y-10">
            <div className="max-w-xl">
                <h1 className="text-4xl font-extrabold text-slate-900 tracking-tight mb-3 italic">Predictive Intelligence.</h1>
                <p className="text-slate-500 font-medium leading-relaxed">
                    Leverage our locally-hosted prediction engine to estimate future outcomes. Adjust performance sliders
                    and simulate &quot;what-if&quot; scenarios for upcoming subjects.
                </p>
            </div>

            <PredictiveCGPADashboard
                domain={domain}
                userGrades={userGrades}
                overrides={overrides}
                updateOverride={updateOverride}
                clearOverrides={clearOverrides}
            />
        </div>
    );
}
